export default {
	data() {
		return {
			userInfo: {}
		};
	},
	onShow() {
		console.log('onShow');
		this.userInfo = uni.getStorageSync('userinfo');
		console.log('userInfo' + JSON.stringify(this.userInfo));
	},
	methods: {
		// 跳转到登录页面
		goLogin() {
			uni.navigateTo({
				url: '../login/index'
			});
		},
		//退出登录
		userSignOut() {
			uni.removeStorageSync('userinfo');
			uni.removeStorageSync('token');
			uni.navigateTo({
				url: '../index/index'
			});
			uni.showToast({
				title: '退出登录成功',
				duration: 2000
			});
		}
	}
};
